import React, {useEffect} from 'react';
import {  
  Switch,
  Route,
  useLocation
} from 'react-router-dom';
import {
  ProtectedRoute,
  UserProtectedRoute
} from '../components';
import {history} from '../services';
import {
  MainPage,
  LoginPage,
  RegisterPage,
  ForgotPasswordPage,
  ResetPasswordPage,
  FeedPage,
  IngredientPage,
  IngredientModalPage,
  OrderPage,
  OrderModalPage,
  ProfilePage,
  NotFoundPage
} from '../pages';

type TLocation = {
  state: {
    background: any
  }
};

const publicPath = process.env.PUBLIC_URL || '';

export function MainRouter() {
  const location: TLocation = useLocation();
  const background = location.state && location.state.background;
  useEffect(() => {
    history.replace({...history.location, state: null})
  }, []);
  return (
    <>
      <Switch location={background || location}>
        <Route
          exact
          path={`${publicPath}/`}
          render={() => (
            <MainPage />
          )}
        />
        <Route
          exact
          path={`${publicPath}/ingredients/:id`}
          render={() => (
            <IngredientPage />
          )}
        />
        <UserProtectedRoute
          exact
          path={`${publicPath}/login`}
        >
          <LoginPage />
        </UserProtectedRoute>
        <UserProtectedRoute
          exact
          path={`${publicPath}/register`}
        >
          <RegisterPage />
        </UserProtectedRoute>
        <UserProtectedRoute
          exact
          path={`${publicPath}/forgot-password`}
        >
          <ForgotPasswordPage />
        </UserProtectedRoute>
        <UserProtectedRoute
          exact
          path={`${publicPath}/reset-password`} 
        >
          <ResetPasswordPage />
        </UserProtectedRoute>
        <Route
          exact
          path={`${publicPath}/feed`}
          render={() => (
            <FeedPage />
          )}
        />
        <Route
          exact
          path={`${publicPath}/feed/:id`}
          render={() => (
            <OrderPage />
          )}
        />
        <ProtectedRoute
          path={`${publicPath}/profile`}
        >
          <ProfilePage />
        </ProtectedRoute>
        <Route
          path={`${publicPath}/`}
          render={() => (
            <NotFoundPage />
          )}
        />
      </Switch>
      {
        background &&      
        <Route
          exact
          path={`${publicPath}/feed/:id`}
          render={() => (
            <OrderModalPage onClose={history.goBack} />
          )}
        />
      }
      {
        background &&
        <Route
          exact
          path={`${publicPath}/profile/orders/:id`}>
            <OrderModalPage onClose={history.goBack} />
        </Route>
      }
      {
        background &&
        <Route
          exact
          path={`${publicPath}/ingredients/:id`}>
            <IngredientModalPage caption=" " onClose={history.goBack} />
        </Route>
      }  
    </>
  )
}

